import React, {Component} from "react";
import {
  GoogleMap,
  DirectionsRenderer,
  DirectionsService,
  DirectionsServiceProps,
} from "@react-google-maps/api";
import { withProps, compose, lifecycle } from "recompose";
import {googleMapsApiKey} from '../../../allconfig';

const mapURL = `https://maps.googleapis.com/maps/api/js?key=${googleMapsApiKey}&v=3.exp&libraries=geometry,drawing,places`
//default value for lat, long
const defaultPosition = {
    lat: 4.0004737, 
    lng: 9.8057055
}

const start = {
    lat: 4.060454737, 
    lng: 9.8057055
}

const end = {
    lat: 4.015304737, 
    lng: 9.5057055
}

const MapWithADirectionsRenderer = compose(
  withProps({
    googleMapURL: {mapURL},
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `500px` }} />,
    mapElement: <div style={{ height: `100%` }} />
  }),
  
  lifecycle({
    componentDidMount() {
      DirectionsService.route(
        {
          origin: {start},
          destination: {end},
          travelMode: 'DRIVING'
        },
        (result, status) => {
          if (status === DirectionsStatus.OK) {
            this.setState({
              directions: result
            });
          } else {
            console.error(`error fetching directions ${result}`);
          }
        }
      );
    }
  })
)(props => (
  <GoogleMap
    defaultZoom={10}
    defaultCenter={defaultPosition}
  >
    {props.directions && <DirectionsRenderer directions={props.directions} />}
  </GoogleMap>
));

class Test extends Component {
  render() {
    return (
      <>
        <MapWithADirectionsRenderer />
      </>
    );
  }
}

export default Test;
